import { create } from 'zustand';
import { persist } from 'zustand/middleware';

/**
 *   
 */
export interface UserInfo {
  email: string;
  name: string;
  picture: string | null;
}

/**
 *    
 */
export interface LoginCredential {
  idToken: string;
  user: UserInfo;
}

/**
 *   
 */
export interface AuthState {
  /**    */
  isAuthenticated: boolean;

  /** Google ID  */
  idToken: string | null;

  /**   */
  user: UserInfo | null;

  /**   */
  error: string | null;

  /**   */
  isLoading: boolean;

  // Actions
  login: (credential: LoginCredential) => void;
  logout: () => void;
  getIdToken: () => string | null;
  reset: () => void;
}

/**
 * Google OAuth   
 * VITE_GOOGLE_CLIENT_ID   false
 */
const GOOGLE_CLIENT_ID = import.meta.env.VITE_GOOGLE_CLIENT_ID || '';
export const isGoogleAuthEnabled = GOOGLE_CLIENT_ID !== '' && GOOGLE_CLIENT_ID !== 'YOUR_GOOGLE_CLIENT_ID';

/**
 *  
 * Google OAuth      
 */
const initialState = {
  isAuthenticated: !isGoogleAuthEnabled, // Google OAuth   
  idToken: null,
  user: isGoogleAuthEnabled ? null : { email: 'local@localhost', name: 'Local User', picture: null },
  error: null,
  isLoading: false,
};

/**
 *    
 *
 * Google OAuth     .
 * localStorage    .
 */
export const useAuthStore = create<AuthState>()(
  persist(
    (set, get) => ({
      ...initialState,

      login: (credential: LoginCredential) => {
        set({
          isAuthenticated: true,
          idToken: credential.idToken,
          user: credential.user,
          error: null,
          isLoading: false,
        });
      },

      logout: () => {
        set({
          isAuthenticated: false,
          idToken: null,
          user: null,
          error: null,
          isLoading: false,
        });
      },

      getIdToken: () => {
        return get().idToken;
      },

      reset: () => {
        set({ ...initialState });
      },
    }),
    {
      name: 'estelle-auth', // localStorage  
      partialize: (state) => ({
        //    (error, isLoading )
        isAuthenticated: state.isAuthenticated,
        idToken: state.idToken,
        user: state.user,
      }),
      // Google OAuth        
      merge: (persistedState, currentState) => {
        if (!isGoogleAuthEnabled) {
          return { ...currentState, ...initialState };
        }
        return { ...currentState, ...(persistedState as Partial<AuthState>) };
      },
    }
  )
);
