import { useState, useEffect, useCallback, useRef } from 'react';
import { cn } from '../../lib/utils';

// ============================================================================
// Types
// ============================================================================

/**
 *    
 */
export interface SlashCommandResult {
  /**    */
  isSlashCommand: boolean;
  /** "/"    (    ) */
  prefix: string;
  /**     (    ) */
  command: string;
}

// ============================================================================
// Functions
// ============================================================================

/**
 *    
 *
 *   :
 * 1.     "/"     
 * 2. "/"     (   =  )
 *
 * @param text -  
 * @returns    
 */
export function parseSlashCommand(text: string): SlashCommandResult {
  if (!text) {
    return {
      isSlashCommand: false,
      prefix: '',
      command: '',
    };
  }

  //     "/"   
  let slashIndex = -1;
  for (let i = text.length - 1; i >= 0; i--) {
    const char = text[i];
    if (char === ' ' || char === '\n') {
      //    (  )
      break;
    }
    if (char === '/') {
      slashIndex = i;
      break;
    }
  }

  // "/"   
  if (slashIndex === -1) {
    return {
      isSlashCommand: false,
      prefix: '',
      command: '',
    };
  }

  // "/"   
  const prefix = text.slice(slashIndex);

  //  
  return {
    isSlashCommand: true,
    prefix,
    command: prefix.slice(1), // "/" 
  };
}

/**
 *    prefix 
 *
 * @param commands -    (: ["/compact", "/clear", "/tdd-flow"])
 * @param prefix -   (: "/", "/tdd")
 * @returns   
 */
export function filterSlashCommandsByPrefix(commands: string[], prefix: string): string[] {
  //     
  if (!prefix.startsWith('/')) {
    return [];
  }

  // "/"     
  if (prefix === '/') {
    return commands;
  }

  //      (   )
  if (prefix.includes(' ')) {
    return [];
  }

  //   
  const searchTerm = prefix.toLowerCase();

  //   prefix 
  return commands.filter((cmd) =>
    cmd.toLowerCase().startsWith(searchTerm)
  );
}

// ============================================================================
// Hook Types
// ============================================================================

/**
 * useSlashAutocomplete   
 */
export interface UseSlashAutocompleteResult {
  /**    */
  selectedIndex: number;
  /**     (0  ) */
  moveUp: () => void;
  /**     ( 0 ) */
  moveDown: () => void;
  /**   0  */
  reset: () => void;
}

// ============================================================================
// Hooks
// ============================================================================

/**
 *      
 *
 * @param itemCount -  
 * @returns     
 */
export function useSlashAutocomplete(itemCount: number): UseSlashAutocompleteResult {
  const [selectedIndex, setSelectedIndex] = useState(0);

  // itemCount   
  useEffect(() => {
    if (itemCount === 0) {
      setSelectedIndex(0);
    } else if (selectedIndex >= itemCount) {
      setSelectedIndex(itemCount - 1);
    }
  }, [itemCount, selectedIndex]);

  const moveDown = useCallback(() => {
    if (itemCount === 0) return;
    setSelectedIndex((prev) => (prev + 1) % itemCount);
  }, [itemCount]);

  const moveUp = useCallback(() => {
    if (itemCount === 0) return;
    setSelectedIndex((prev) => (prev - 1 + itemCount) % itemCount);
  }, [itemCount]);

  const reset = useCallback(() => {
    setSelectedIndex(0);
  }, []);

  return {
    selectedIndex,
    moveUp,
    moveDown,
    reset,
  };
}

// ============================================================================
// Components
// ============================================================================

/**
 * SlashAutocompletePopup Props
 */
export interface SlashAutocompletePopupProps {
  /**     */
  commands: string[];
  /**    */
  selectedIndex: number;
  /**     */
  onSelect: (command: string) => void;
  /**    */
  visible: boolean;
}

/**
 *    
 *
 *      
 */
export function SlashAutocompletePopup({
  commands,
  selectedIndex,
  onSelect,
  visible,
}: SlashAutocompletePopupProps) {
  const listRef = useRef<HTMLDivElement>(null);
  const selectedRef = useRef<HTMLButtonElement>(null);

  //     
  useEffect(() => {
    if (visible && selectedRef.current) {
      selectedRef.current.scrollIntoView({
        block: 'nearest',
        behavior: 'smooth',
      });
    }
  }, [selectedIndex, visible]);

  if (!visible || commands.length === 0) {
    return null;
  }

  return (
    <div
      ref={listRef}
      className="absolute bottom-full left-0 mb-1 max-h-48 min-w-[140px] max-w-[200px] overflow-y-auto rounded-lg border border-border bg-popover shadow-lg z-50"
    >
      {commands.map((command, index) => (
        <button
          key={command}
          ref={index === selectedIndex ? selectedRef : undefined}
          type="button"
          className={cn(
            'w-full px-3 py-1.5 text-left text-sm transition-colors',
            'hover:bg-accent hover:text-accent-foreground',
            index === selectedIndex && 'bg-accent text-accent-foreground'
          )}
          onClick={() => onSelect(command)}
          onMouseDown={(e) => e.preventDefault()} //  
        >
          <span className="font-mono text-xs">{command}</span>
        </button>
      ))}
    </div>
  );
}
